@import "index";

.homeWrapper {
  padding-bottom: 40px;
}

.homeHeader {
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
  align-items: center;
}
.video {
  width: 100%;
}
.homeHeader::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(13, 7, 17, 0.7);
}

.homeHeaderContent {
  align-items: center;
  text-align: center;
  position: absolute;
  z-index: 1;
  @media (max-width: 425px) {
    row-gap: 10px;
    > h4 {
      font-size: 20px;
    }
  }
}
.choiceData {
  > div {
    flex: 1 1 300px;
    max-width: 400px;
    @media (max-width: 768px) {
      text-align: center;
      align-items: center;
    }
    > img {
      width: 60px;
      height: 80px;
    }
  }
}

.approachData {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
  > div {
    flex: 1 1 300px;
    max-width: 500px;
    h5 {
      padding-bottom: 5px;
      border-bottom: 1px solid #000;
    }
    @media (max-width: 768px) {
      text-align: center;
      align-items: center;
    }
  }
}

.elevateBusinessBlock {
  span {
    display: flex;
    align-items: center;
    column-gap: 5px;
    > img {
      max-width: 25px;
    }
  }
}